import s from './Navbar.module.css'
import Setting from "./Setting/Setting";
import {NavLink} from "react-router-dom";
import NavbarPostContainer from "./navbarPosts/NavbarPostsContainer";


let Navbar = () => {
    return (
        <nav className={s.nav}>
            <div className={s.navDiv}>
                <NavLink to={'/profile'} activeClassName={s.active}>Profile </NavLink>
            </div>
            <div className={s.navDiv}>
                <NavLink to={'/dialogs'} activeClassName={s.active}>Massages</NavLink>
            </div>
            <div className={s.navDiv}>
                <NavLink to={"/nevs"} activeClassName={s.active}>Nevs</NavLink>
            </div>
            <div className={s.navDiv}>
                <NavLink to={"/music"} activeClassName={s.active}>Music</NavLink>
            </div>
            <div className={s.navDiv}>
                <NavLink to={"/users"} activeClassName={s.active}>Find Users </NavLink>
            </div>
            <Setting/>
            <NavbarPostContainer/>
        </nav>
    )
}

export default Navbar;