import React from 'react';
import Profile from "./profile";
import { connect } from "react-redux";
import { getUser, requestStatus, updateStatus, savePhoto, saveProfile} from "../../Redux/profileReducer";
import {  withRouter } from "react-router-dom";
import { compose } from 'redux';
import { getProfile, getUserId, getStatus, getIsPreloaderToggled } from '../../Redux/selectors/Profile-selector';



class ProfileContainer extends React.Component {

    constructor(props) {
        super(props);
        this.userId = null
    }

    refreshProfile = () => {
        const { match, userId, history, getUser, requestStatus } = this.props;
        this.userId = match.params.userId;
        if (!this.userId) {
            this.userId = userId;

            if (!this.userId) {
                history.push('/login');
            }
        }

        getUser(this.userId);
        requestStatus(this.userId);
    }

    componentDidMount() {
        this.refreshProfile()
    }

    componentDidUpdate(prevProps, prevState) {
        this.userId = this.props.match.params.userId;
        if (this.userId !== prevProps.match.params.userId) {
            this.refreshProfile()
        }
    }

    render() {
        return (
            <div>
                <Profile {...this.props} isOwner={this.props.match.params.userId} />
            </div>
        )

    }
}




let mapStateToProps = (state) => {
    return {
        profile: getProfile(state),
        status: getStatus(state),
        userId: getUserId(state),
        isPreloaderToggled: getIsPreloaderToggled(state),
    }
};

export default compose(
    connect(mapStateToProps, { getUser, requestStatus, updateStatus, savePhoto, saveProfile, getIsPreloaderToggled }),
    withRouter,
)(ProfileContainer);





