import { followApi, usersApi } from "../API/Api";
import { updateObjectInArray } from './../utils/object-helpers/object-helpers';

const FOLLOW = "userReducer/FOLLOW";
const UNFOLLOW = "userReducer/UNFOLLOW";
const SET_USERS = "userReducer/SET_USERS";
const SET_CURRENT_PAGE = "userReducer/SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "userReducer/SET_TOTAL_USERS_COUNT";
const SET_IS_FETCHING = "userReducer/SET_IS_FETCHING";
const TOGGLE_FOLLOWING_PROGRESS = "userReducer/TOGGLE_FOLLOWING_PROGRESS";

let initialState = {
    users: [],
    pageSize: 10,
    totalItemsCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: []
};



const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state, users: updateObjectInArray(state.users, action.userId, 'id', { followed: true })
            }
        case UNFOLLOW:
            return {
                ...state, users: updateObjectInArray(state.users, action.userId, 'id', { followed: false })
            }
        case SET_USERS:
            return {
                ...state,
                users: action.users
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage,
            }
        case SET_TOTAL_USERS_COUNT:
            return {
                ...state,
                totalItemsCount: action.count,
            }
        case SET_IS_FETCHING:
            return {
                ...state,
                isFetching: action.isFetching,
            }
        case TOGGLE_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state;

    }
};


const followSuccess = (userId) => ({ type: FOLLOW, userId });
const unFollowSuccess = (userId) => ({ type: UNFOLLOW, userId });
const setUsers = users => ({ type: SET_USERS, users: users });
const setCurrentPage = currentPage => ({ type: SET_CURRENT_PAGE, currentPage });
const togglePreLoader = isFetching => ({ type: SET_IS_FETCHING, isFetching });
const toggleFollowingProgress = (isFetching, userId) => ({ type: TOGGLE_FOLLOWING_PROGRESS, isFetching, userId });
const setTotalUsersCount = count => ({type: SET_TOTAL_USERS_COUNT, count});


export const requestUsers = (currentPage, pageSize) => {
    return async (dispatch) => {
        dispatch(setCurrentPage(currentPage));
        dispatch(togglePreLoader(true));
        const data = await usersApi.getUsers(currentPage, pageSize)
        dispatch(togglePreLoader(false));
        dispatch(setUsers(data.items));
        dispatch(setTotalUsersCount(data.totalCount));
    }
};


const FollowUnf = async (userId, dispatch, ApiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId));
    const data = await ApiMethod(userId)
    if (data.resultCode === 0) {
        dispatch(actionCreator(userId));
    }
    dispatch(toggleFollowingProgress(false, userId));
}


export const follow = (userId) => {
    return async (dispatch) => {
        const ApiMethod = followApi.follow.bind(followApi)
        FollowUnf(userId, dispatch, ApiMethod, followSuccess)
    }
};


export const unFollow = (userId) => {
    return async (dispatch) => {
        FollowUnf(userId, dispatch, followApi.unFollow.bind(followApi), unFollowSuccess)
    }
};



export default usersReducer;