import s from './HeaderMenu.module.css';

const HeaderMenu = ({toggleMenu, logout}) => {

    const onLogout = () => {
        logout()
    };

    return (
        <div className={s.nav} onClick={toggleMenu}>
            <div className={s.firstLi}>option 1</div>
            <div>option 2</div>
            <div className={s.signOut} onClick={onLogout}>
                sign out</div>
        </div>
    )
}


export default HeaderMenu;