import './App.css';
import Navbar from "./companents/Navbar/navbar";
import Music from "./companents/Music/Music";
import News from "./companents/Nevs/Nevs";
import SettingsContainer from "./companents/Settings/settingsContainer";
import { HashRouter, Redirect, Switch } from "react-router-dom";
import { Route } from "react-router-dom";
import ProfileContainer from "./companents/Profile/profileContainer";
import HeaderContainer from "./companents/Header/HeaderContainer";
import { connect } from 'react-redux';
import Preloader from './companents/common/preloader/Preloader';
import React from 'react';
import { authorize } from './Redux/app-reducer';
import { Provider } from "react-redux";
import store from "./Redux/redux-store";
import NotFound from './companents/NotFound/NotFound';
import ErrorBoundary from './companents/common/ErrorBoundary/ErrorBoundary';

const DialogsContainer = React.lazy(() => import('./companents/Dialogs/DialogsContainer'));
const UsersContainer = React.lazy(() => import('./companents/Users/UsersContainer'));
const LoginContainer = React.lazy(() => import('./companents/login/LoginContainer'));



// ! package look json


class App extends React.Component {

    onHandleError = (event) => {
        alert(event.reason)
    }

    componentDidMount = () => {
        this.props.authorize();
    }

    render() {

        // if (!this.props.authorized) {
        //     return <Preloader uniqueClassName={'AppPreloader'} />
        // }

        return (
            <HashRouter>
                <React.Suspense fallback={<div className={'suspense'}><div>loading ...</div><Preloader uniqueClassName={'suspensePre'} /></div>}>
                    <div className="app-wrapper">
                        <HeaderContainer />
                        <Navbar />
                        <div className={'app-wrapper-content'}>
                            <Switch>
                                <Route exact path={'/'}><Redirect to={'/profile'} /></Route>
                                <Route path={'/profile/:userId?'}
                                    render={() => <ErrorBoundary>
                                        <ProfileContainer />
                                    </ErrorBoundary>
                                    } />
                                <Route path={'/dialogs'}
                                    render={() => <ErrorBoundary>
                                        <DialogsContainer />
                                    </ErrorBoundary>
                                    } />
                                <Route path={'/users'} render={
                                    () => <ErrorBoundary>
                                        <UsersContainer />
                                    </ErrorBoundary>
                                } />
                                <Route path={'/login'} render={() => <ErrorBoundary>
                                        <LoginContainer/>
                                    </ErrorBoundary>} />
                                <Route path={'/settings'} component={SettingsContainer} />
                                <Route path={'/nevs'} component={News} />
                                <Route path={'/music'} component={Music} />
                                <Route exact path={'*'} render={NotFound} />
                            </Switch>
                        </div>
                    </div>
                </React.Suspense>
            </HashRouter>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        authorized: state.app.authorized
    }
};

const AppContainer = connect(mapStateToProps, { authorize })(App);

const SamuraiJsApp = (props) => {
    return (
        <Provider store={store}>
            <AppContainer />
        </Provider>
    )
}

export default SamuraiJsApp;