import s from './profile.module.css'
import MainImg from "./MeinImg/MainImg";
import UserInfo from "./User-Info/User_Info";
import MyPostsContainer from "./MyPosts/MyPostsContainer";

let Profile = ({profile, status,updateStatus, isOwner, savePhoto, saveProfile, isPreloaderToggled}) => {

    

    return (
        <div>
            <MainImg/>
            <UserInfo profile={profile} 
            status={status}
            updateStatus={updateStatus}
            isOwner={isOwner}
            savePhoto={savePhoto}
            saveProfile={saveProfile}
            isPreloaderToggled={isPreloaderToggled}/>
            <MyPostsContainer isOwner={isOwner}/>
        </div>
    )
}

export default Profile;