import s from './header.module.css'
import HeaderLogo from "./Header_img/Header_img";
import { NavLink } from "react-router-dom";
import HeaderMenu from './HeaderMenu/HeaderMenu';
import Preloader from '../common/preloader/Preloader';


let Header = ({isAuth, onBlurHandler, toggleMenu, logout, isOpen, userProfilePhoto}) => {
    return (
        <header className={`${s.header} ${s.header2}`}>
            <HeaderLogo />
            <div className={s.login}>
                {isAuth ? <button className={s.userPhoto} onBlur={onBlurHandler} onClick={toggleMenu}>
                 {userProfilePhoto ? <img src={userProfilePhoto} alt={'userPhoto'}/> : <Preloader uniqueClassName={'HeaderPre'}/>}
                </button>
                    : <NavLink to={'./login'}>Login</NavLink>}

            </div>
             {isOpen && <HeaderMenu logout={logout} />}
        </header>
    )
}

export default Header;