import s from './MainImg.module.css'
import React from 'react';

let MainImg = React.memo(() => {
    return (
        <div className={s.MainImg}>
                <img src={'https://www.imgworlds.com/wp-content/uploads/2015/12/generic.jpg'} alt={'mainImg'}/>
        </div>
    )
})

export default MainImg;