import s from './User_Info.module.css'
import Preloader from "../../common/preloader/Preloader";
import userPhoto from './../../../image/photo.jpg';
import ProfileData from './ProfileData/ProfileData';
import { useState } from 'react';
import FormData from './FormData/FormData';
import { MdAddToPhotos } from 'react-icons/md';


const UserInfo = (props) => {

    const [editMode, setEditMode] = useState(false);

    if (!props.profile) {
        return <Preloader uniqueClassName={'ProfilePreloader'} />
    }


    const userImg = props.profile.photos.large ? props.profile.photos.large : userPhoto;


    const onPhotoSelected = (e) => {
        const photoFile = e.currentTarget.files;
        if (photoFile.length) {
            props.savePhoto(photoFile[0])
        }
    }

    const onSubmit = (formData) => {
        props.saveProfile(formData)
        .then(()=> {
            setEditMode(false)
        })
               
    }

    return (
        <div className={s.userInfo}>
            <div className={s.img}>
                <img src={userImg} alt={'userImg'} />
                {!props.isOwner && <div> <input type='file' onChange={onPhotoSelected} className={s.photoFile} 
                 id="chose file" /> 
                 <label htmlFor={'chose file'} className={s.addPhotoStyle}><MdAddToPhotos className={s.addPhotoIcon}/>add photo</label></div>}
                 {props.isPreloaderToggled && <Preloader uniqueClassName={'addPhotoPre'}/>}
            </div>
            {editMode
                ? <FormData onSubmit={onSubmit}
                    initialValues={props.profile}
                    profile={props.profile}
                    toggleEditMode={() => { setEditMode(false) }} />
                : <ProfileData profile={props.profile}
                    isOwner={props.isOwner}
                    toggleEditMode={() => { setEditMode(true) }}
                    status={props.status}
                    updateStatus={props.updateStatus} />}
        </div>
    )
}


export default UserInfo;


