import ProfileStatus from './ProfileStatus/ProfileStatus';
import Contact from './Contacts/contacts';
import s from './ProfileData.module.css';
import { FaUserEdit } from 'react-icons/fa';


const ProfileData = (props) => {
    const contacts = Object.keys(props.profile.contacts).map(key =>
        <Contact key={key} contactTitle={key} contactValue={props.profile.contacts[key]} />);

    return (
        <div className={s.profileData}>
               <div className={s.aboutUser}>
                <div className={s.name}>
                    <b>{props.profile.fullName}</b>
                </div>
                <div>
                    <ProfileStatus status={props.status} updateStatus={props.updateStatus} />
                </div>
                <div>
                    <span className={s.aboutMe}>aboutMe: </span>
                    {props.profile.aboutMe ? props.profile.aboutMe : '...'}
                </div>

                <div>
                    <span className={s.lookingForAJob}>lookingForAJob: </span>
                    {props.profile.lookingForAJob ? 'yes' : 'no'}
                </div>
                {props.profile.lookingForAJob && <div>
                    <span className={s.lookingForAJobDescription}>my professional skills: </span>
                    {props.profile.lookingForAJobDescription ? props.profile.lookingForAJobDescription
                        : '...'}
                </div>}
                {!props.isOwner ? <button onClick={props.toggleEditMode} className={s.button}><FaUserEdit/> edit</button> : null}
            </div>

            <div className={s.contacts}>
                <div className={s.title}>contacts</div>
                {contacts}
            </div>
        </div>
    )
};


export default ProfileData;