import { maxLengthCreator, required } from '../../../../utils/validators/Validators';
import { Field, reduxForm } from 'redux-form';
import { Element } from './../../../common/formControls/formControls';
import s from './MyPostsForm.module.css'

const maxLength20 =  maxLengthCreator(20);
const Textarea = Element('textarea')

const PostForm = ({handleSubmit}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div>
                <Field className={s.textarea} name={'newPostText'} component={Textarea} placeholder={'post message'}
                       validate={[required, maxLength20]}></Field>
            </div>
            <div>
               <button className={s.button}>
                    add
               </button>
            </div>
        </form>
    )
}


export default reduxForm({form: 'ProfilePostForm'})(PostForm);
