import { getAuthUserData } from "./authReducer";

const AUTHORIZED_SUCCESS = 'appReducer/AUTHORIZED_SUCCESS';


type InitialStateType = {
    authorized: Boolean
}

let initialState: InitialStateType = {
    authorized: false,
};


const appReducer = (state = initialState, action:any): InitialStateType => {
    switch (action.type) {
        case AUTHORIZED_SUCCESS:
            return {
                ...state,
                authorized: true
            }; 
        default:
            return state;
    }
};



type AuthorizedSuccessActionType = {
    type: typeof AUTHORIZED_SUCCESS
}
const authorizedSuccess = (): AuthorizedSuccessActionType => ({ type: AUTHORIZED_SUCCESS});



export const authorize = () => {
    return (dispatch: any) => {
      let dispatchResult = dispatch(getAuthUserData());

        Promise.all([dispatchResult])
        .then(() => {
            dispatch(authorizedSuccess())
        })
    }
};




export default appReducer;