import React from "react";
import Header from "./header";
import { connect } from "react-redux";
import { logout, requestAuthUserProfile } from "../../Redux/authReducer";
import { compose } from "redux";
import { getProfilePhoto, getLogin } from "../../Redux/selectors/header-selector";
import { getIsAuth } from "../../Redux/selectors/auth-selector";


class HeaderContainer extends React.Component {

    constructor(props) {
        super(props);
        this.state = { isOpen: false };
        this.clearId = null
    }

    

    componentDidMount() {
        const { requestAuthUserProfile, isAuth } = this.props;
        if(isAuth) {
            requestAuthUserProfile()
        }
    }

    componentDidUpdate(prevProps) {
        if(this.props.isAuth !== prevProps.isAuth) {
            this.props.requestAuthUserProfile()
        }
    }

    
    toggleMenu = () => {
        this.setState(state => ({
            isOpen: !state.isOpen
        }))
    }

    onBlurHandler = () => {
        this.clearId = setTimeout(() => {
            this.setState({
                isOpen: false
            })
        }, 150);
    }


    render() {
        return <Header {...this.props} isOpen={this.state.isOpen} toggleMenu={this.toggleMenu}
            onBlurHandler={this.onBlurHandler} />
    }
}


const mapStateToProps = (state) => ({
    isAuth: getIsAuth(state),
    login: getLogin(state),
    userProfilePhoto: getProfilePhoto(state)
})


export default compose(
    connect(mapStateToProps, { logout, requestAuthUserProfile })
)(HeaderContainer) 