import * as axios from "axios";


const instance = axios.create({
    withCredentials: true,
    headers: {
        'API-KEY': '3e3df446-1297-4960-972a-76fda8f26dac'
    },
    baseURL: 'https://social-network.samuraijs.com/api/1.0/'
});



const usersApi = {
    async getUsers(currentPage = 1, pageSize = 10) {
        const result = await instance.get(`users?page=${currentPage}&count=${pageSize}`)
        return result.data;
    }
};


const authApi = {
    async me() {
        const result = await instance.get('auth/me')
        return result.data
    },

    async login(email, password, rememberMe, captcha=null) {
        const result = await instance.post('auth/login', {
            email,
            password,
            rememberMe,
            captcha
        })
        return result.data
    },

    async logout() {
        const response = await instance.delete('/auth/login')
        return response.data
    }
}


const profileApi = {
    async getProfile(userId) {
        const result = await instance.get(`profile/` + userId)
        return result.data;
    },

    async getStatus(userId) {
        const response = await instance.get(`profile/status/${userId}`)
        return response.data
    },

    async updateStatus(status) {
        const response = await instance.put(`profile/status`, { status: status })
        return response.data
    },

    async savePhoto(photoFile) {
        const formData = new FormData()
        formData.append('imag', photoFile)
        const response = await instance.put('profile/photo', formData, {
            headers: {
                'content-Type': 'multipart/form-data'
            } 
        })
        return response.data
    },

    async saveProfile(profile) {
        const response = await instance.put('profile', profile)
        return response.data
    }
};



const followApi = {
    follow(id) {
        return instance.post(`follow/${id}`)
            .then(result => result.data);
    },
    unFollow(id) {
        return instance.delete('follow/' + id)
            .then(result => result.data)
    }
}




const securityApi = {
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`)
            .then(result => result.data);
    },
}


export { usersApi, authApi, profileApi, followApi, securityApi};