import s from './MyPosts.module.css'
import Post from './Post/Post';
import React from 'react';
import AddProfilePostForm from './MyPostForm/MyPostForm';

const MyPosts = ({ postsData, addPost, deletePost, isOwner }) => {

    const postsElements = postsData.map(p =>
        <Post key={p.id}
            id={p.id}
            post={p.post}
            likesCount={p.likesCount}
            img={p.img}
            deletePost={deletePost}
            isOwner={isOwner} />);

    const otherPosts = postsElements;

    const newPostText = (values) => {
        addPost(values.newPostText);
    }


    return (
        <div>
            {!isOwner 
            ? <div className={s.MyForm}>
                <div className={s.f_text}>
                    <h3>My post</h3>
                </div>
                <AddProfilePostForm onSubmit={newPostText} />
                {postsElements}
            </div>
            : <div className={s.otherPosts}>
                <h1>
                   posts
                </h1>
                {otherPosts}
            </div>}
        </div>
    )
}

export default MyPosts;