import { stopSubmit } from 'redux-form';
import { profileApi } from '../API/Api';
import { requestAuthUserProfile } from './authReducer';

const ADD_POST = 'profileReducer/ADD_POST';
const SET_USER_PROFILE = 'profileReducer/SET_USER_PROFILE';
const SET_STATUS = 'profileReducer/SET_STATUS';
const DELETE_POST = 'profileReducer/DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'profileReducer/SAVE_PHOTO_SUCCESS';
const TOGGLE_PRELOADER = 'profileReducer/TOGGLE_PRELOADER';



let initialState = {
    postsData: [
        {
            id: 1, post: 'hello this is a my first post', likesCount: 20,
        },
        {
            id: 2, post: 'hello this is a my second post', likesCount: 10,
        },
    ],
    profile: null,
    status: '',
    isPreloaderToggled: false,
};


const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST:
            const newPost = {
                id: 3,
                post: action.newPostText,
                likesCount: 0,
            };
            return {
                ...state,
                postsData: [...state.postsData, newPost],
            };
        case SET_USER_PROFILE:
            return {
                ...state,
                profile: action.user,
            };
        case SET_STATUS:
            return {
                ...state,
                status: action.status,
            }
        case DELETE_POST:
            return {
                ...state, postsData: state.postsData.filter(p => p.id !== action.postId)
            }
        case SAVE_PHOTO_SUCCESS:
            return {
                ...state, profile: {...state.profile, photos: action.photo}
            }
        case TOGGLE_PRELOADER:
            return {
                ...state,
                ...action.payload
            }  
        default:
            return state;
    }
};



export const addPost = (newPostText) => ({ type: ADD_POST, newPostText });
export const setUserProfile = (user) => ({ type: SET_USER_PROFILE, user });
export const setStatus = status => ({ type: SET_STATUS, status })
export const deletePost = postId => ({ type: DELETE_POST, postId })
export const savePhotoSuccess = photo => ({type: SAVE_PHOTO_SUCCESS, photo})
export const togglePreloaderProfile = isPreloaderToggled => ({type: TOGGLE_PRELOADER, payload: {isPreloaderToggled}})



export const getUser = (userId) => {
    return async (dispatch) => {
        const data = await profileApi.getProfile(userId)
        dispatch(setUserProfile(data))
    }
};


export const requestStatus = (userId) => {
    return async (dispatch) => {
        const response = await profileApi.getStatus(userId)
        dispatch(setStatus(response))
    }
};


export const updateStatus = (status) => {
    return async (dispatch) => {
        const response = await profileApi.updateStatus(status)
        if (response.resultCode === 0) {
            dispatch(setStatus(status))
        }
    }
};


export const savePhoto = (photoFile) => {
    return async (dispatch) => {
        dispatch(togglePreloaderProfile(true))
        const result = await profileApi.savePhoto(photoFile)
        if(result.resultCode === 0) {
            dispatch(togglePreloaderProfile(false))
            dispatch(savePhotoSuccess(result.data.photos))
            dispatch(requestAuthUserProfile())
        }
    }
}


export const saveProfile = (profile) => {
    return async (dispatch, getState) => {
        const userId = getState().auth.userId; 
        const result = await profileApi.saveProfile(profile)
        if(result.resultCode === 0) {
            dispatch(getUser(userId))
        } else {
            
            const message = result.messages.length > 0 ? result.messages[0] : 'some error';
            const action = stopSubmit('edit-profile', { _error: message});
            dispatch(action)
            return Promise.reject( result.messages[0] )
        }
    }
}



export default profileReducer;