//import { stopSubmit } from 'redux-form';
import { authApi, profileApi, securityApi } from '../API/Api';

const SET_USERS_DATA = 'authReducer/SET_USERS_DATA';
const GET_PROFILE_SUCCESS ='authReducer/GET_PROFILE_SUCCESS';
const GET_CAPTCHA_URL_SUCCESS = 'authReducer/GET_CAPTCHA_URL_SUCCESS';
const SET_ERROR_MESSAGE = 'authReducer/SET_ERROR_MESSAGE';


type ContactsType = {
github: string
vk: string
facebook: string
instagram: string
twitter: string
website: string
youtube: string
mainLink: string
}

type AuthUserProfileType = {
    userId: number
    lookingForAJob: boolean
    lookingForAJobDescription: string
    fullName: string
    contacts: ContactsType
}

const initialState = {
    userId: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: false,
    authUserProfile: null as AuthUserProfileType | null,
    captchaUrl: null as string | null,
    errorMessage: null as string | null
};

type InitialStateType = typeof initialState;


const authReducer = (state = initialState, action: any): InitialStateType => {
    switch (action.type) {
        case GET_CAPTCHA_URL_SUCCESS:
        case SET_USERS_DATA:
        case SET_ERROR_MESSAGE:    
            return {
                ...state,
                ...action.payload,
            };
        case GET_PROFILE_SUCCESS:
            return {
                ...state, authUserProfile: action.authUserProfile
            }    
        default:
            return state;
    }
};


const setUserData = (userId: number | null, email: string | null, login: string | null, isAuth: boolean) =>
    ({ type: SET_USERS_DATA, payload: { userId, email, login, isAuth } });
const getProfileSuccess = (authUserProfile: AuthUserProfileType) => ({type: GET_PROFILE_SUCCESS, authUserProfile})
const getCaptchaUrlSuccess = (captchaUrl: string | null) => ({type: GET_CAPTCHA_URL_SUCCESS, payload: {captchaUrl}})
const setErrorMessage = (errorMessageText: string | null) => ({type: SET_ERROR_MESSAGE, payload: {errorMessageText}})


export const getAuthUserData = () => {
    return async (dispatch: any) => {
        const data = await authApi.me()
        if (data.resultCode === 0) {
            let { id, login, email } = data.data;
            dispatch(setUserData(id, email, login, true));
        }
    }
};


const getCaptchaUrl = () => async (dispatch: any) => {
    const result = await securityApi.getCaptchaUrl()
    const captchaUrl = result.url;
    dispatch(getCaptchaUrlSuccess(captchaUrl))
}


export const login = (email: string, password: number, rememberMe: boolean, captcha: string) => {
    return async (dispatch: any) => {
        const data = await authApi.login(email, password, rememberMe, captcha)
        if (data.resultCode === 0) {
            dispatch(getAuthUserData());
            dispatch(getCaptchaUrlSuccess(null))
            dispatch(setErrorMessage(null))
        } else {
            if(data.resultCode === 10) {
                dispatch(getCaptchaUrl())
            }
            const message = data.messages.length > 0 ? data.messages[0] : 'some error';
           // const action = stopSubmit('login', { _error: message });
            //dispatch(action)
            dispatch(setErrorMessage(message))
        }
    }
};



export const logout = () => {
    return async (dispatch: any) => {
        const data = await authApi.logout()
        if (data.resultCode === 0) {
            dispatch(setUserData(null, null, null, false));
        }
    }
};




export const requestAuthUserProfile = () => {
    return async (dispatch: any, getState: any) => {
        const authUserId = getState().auth.userId
        const data = await profileApi.getProfile(authUserId)
            dispatch(getProfileSuccess(data));
    }
};


export default authReducer;