import { Field, reduxForm } from "redux-form";
import { maxLengthCreator, required } from "../../../../utils/validators/Validators";
import { Element } from "../../../common/formControls/formControls";
import style from "../../../common/formControls/formControls.module.css";
import s from "./FormData.module.css";
import { HiOutlineSave } from 'react-icons/hi';
import { GrClose } from 'react-icons/gr';


const Input = Element('input')
const maxLength30 = maxLengthCreator(30)
const Textarea = Element('textarea')

const FormData = (props) => {

    const contactForm = Object.keys(props.profile.contacts).map(key =>
        <div key={key} className={`${s.field} ${s.inputWidth}`}>
            <label htmlFor={key}>{key}</label>
            <Field className={s[key]} name={`contacts.${key}`} component={Input} placeholder={key} id={key} />
        </div> 
    )

    return (
        <form onSubmit={props.handleSubmit} className={s.form}>
            <div className={s.aboutUser}>
                <button className={s.button}><HiOutlineSave /> save</button>
                <div className={`${s.field} ${s.inputWidth}`}>
                    <label htmlFor={'fullName'}>fullName</label>
                    <Field className={s.fullName} placeholder={'fullName'} name='fullName' component={Input}
                        id={'fullName'} validate={[required, maxLength30]} />
                </div>
                <div className={`${s.field} ${s.inputWidth}`}>
                    <label htmlFor={'aboutMe'}>about me</label>
                    <Field className={s.aboutMe} placeholder={'aboutMe'} name='aboutMe' component={Textarea}
                        id={'aboutMe'} validate={[required, maxLength30]} />
                </div>
                <div className={`${s.field} ${s.checkbox}`}>
                    <label htmlFor={'lookingForAJob'}>lookingForAJob</label>
                    <Field className={s.lookingForAJob} type={'checkbox'} name='lookingForAJob'
                     component={Input} id={'lookingForAJob'}  />
                </div>
                <div className={`${s.field} ${s.inputWidth}`}>
                    <label htmlFor={'lookingForAJobDescription'}>my professional skills</label>
                    <Field className={s.lookingForAJobDescription} name='lookingForAJobDescription' component={Textarea}
                     placeholder={'my professional skills'} id={'lookingForAJobDescription'}  />
                </div>
                <div className={s.error}>
                {props.error && <div className={style.formSummaryError}>
                    {props.error}
                </div>}
            </div>
            </div>
            <div className={s.contacts}>
                <div className={s.contactTitle}>
                     Contacts
                </div>
             {contactForm}
            </div>
            <div>
                <GrClose className={s.closeIcon} onClick={props.toggleEditMode}/>
            </div>
        </form>
    )
}


export default reduxForm({ form: 'edit-profile' })(FormData);