import s from './NavbarPosts.module.css'
import NavbarPost from "./navbarPost/navbarPost";


const NavbarPosts = ({navbarData}) => {

    let navbarPost = navbarData.map(n => <NavbarPost key={n.id} name={n.name} img={n.img}/>);

    return (
        <div className={s.navbarPosts}>
            {true ? null : <div><div className={s.header}>
                Friends
            </div>
            <div className={s.post}>
                {navbarPost}
            </div></div>}
        </div>
    )
}

export default NavbarPosts;